<script setup>

import { defineProps, reactive } from "vue";

const props = defineProps(['coords'])

var coords = props.coords

import { ref } from 'vue'


const arctann = ref(-1)
const rtn = ref(-1)




  
      if(navigator.geolocation) {

          setInterval(()=>{
              navigator.geolocation.getCurrentPosition((cords)=>{
                  arctann.value = 180+ ( Math.atan((cords.coords.longitude-coords[1])/(cords.coords.latitude-coords[0])))*(180/Math.PI)
                  if(cords.coords.latitude<coords[0]){
                      arctann.value+=180
                  }
          });
          }, 10)

        
      }













if (rtn.value == -1&&typeof( DeviceMotionEvent ) !== "undefined" && typeof( DeviceMotionEvent.requestPermission ) === "function" ) {
 // (optional) Do something before API request prompt.
   DeviceMotionEvent.requestPermission()
       .then( response => {
       // (optional) Do something after API prompt dismissed.
       if ( response == "granted" ) {
           window.addEventListener( "deviceorientation", (e) => {
            rtn.value = (315-e.webkitCompassHeading)
           })
       }
   })
       .catch( console.error )
}else if(rtn.value == -1) {
   window.addEventListener("deviceorientationabsolute", (e)=>{
     rtn.value = (e.alpha-45)
   });
 }











</script>

